

<template>
 <!--  -->
  <div class="song-detail"><MusicHeader></MusicHeader>
    <div class="head-warp">
      <div class="fl">
        <img :src="details.pic" alt="" />
      </div>
      <div class="rl">
        <div class="info">
          <h1>{{ details.title }}</h1>
          <p class="inf">
            {{ details.desc }}
          </p>
          <div class="tag">
            <!-- <a href="javascript:;">{{ details.tagList }}</a> -->
            <a href="javascript:;" v-for="(t, i) in details.tagList" :key="i">
              # {{ t }}
            </a>
          </div>
        </div>
        <div class="btn">
          <el-button-group>
            <el-button type="danger" icon="el-icon-caret-right" round
              >播放全部</el-button
            >
          </el-button-group>
          <el-button-group>
            <el-button plain round>
              <i class="iconfont icon-heart"></i>
              收藏
            </el-button>
          </el-button-group>
          <el-button-group>
            <el-button round>
              <i class="iconfont icon-share"></i>
              分享
            </el-button>
          </el-button-group>
        </div>
      </div>
    </div>
    <div class="con">
      <div class="title">全部歌曲</div>
      <ul class="lists">
        <li>
          <div class="num">序号</div>
          <div class="song">歌曲</div>
          <div class="artist">歌手</div>
          <div class="album">专辑</div>
          <div class="time">时常</div>
        </li>
        <li v-for="(m, j) in details.trackList" :key="j">
          <div class="num">{{ j + 1 }}</div>
          <div class="song">{{ m.title }}</div>
          <div class="artist">{{ m.artist.name }}</div>
          <div class="album">{{ m.albumTitle }}</div>
          <div class="time">  {{(m.duration/60 >= 10)? '':"0"}}{{Math.floor(m.duration/60)}}: {{(m.duration%60 >=10)? '':"0"}}{{m.duration%60}}  </div>
        </li>
      </ul>
    </div>
    <MusicLive></MusicLive>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Detail",
  data() {
    return {


   
      details: [],
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.getDetails(vm.$route.params.songId);
    });
  },
  methods: {
    getDetails(songId) {
      var url = `/api/v1/tracklist/info?id=${songId}`;
      axios.get(url).then((res) => {
        // console.log(res.data.data);
        this.details = res.data.data;


      });
    },
  },
  beforeRouteUpdate(to, from, next) {
    this.getDetails();
  },
};
</script>

<style scoped>
.song-detail {
  width: 1200px;
  margin: 0 auto;
}

.song-detail .head-warp {
  margin-top: 30px;
  display: flex;
  /* justify-content: space-around; */
  /* justify-content: space-between; */
  width: 100%;
}

.head-warp .fl {
  width: 260px;
  height: 260px;
  margin-right: 60px;
}
.head-warp .fl img {
  width: 100%;
  height: 100%;
}
.head-warp .rl {
  height: 260px;
}
.rl .info {
  height: 210px;
}

.rl .info h1 {
  font-size: 30px;
  font-weight: 600;
}
.rl .info .inf {
  color: #606266;
  margin-top: 15px;
  line-height: 20px;
  max-height: 100px;
}
.rl .info .tag {
  margin-top: 25px;
}
.info .tag a {
  color: #e61723;
  margin-right: 10px;
}
.rl .btn .el-button-group {
  margin-right: 20px;
}
.song-detail .con {
  margin-top: 50px;
}
.con .title {
  font-size: 28px;
  margin-bottom: 30px;
}
.con .lists {
  width: 100%;
}
.con .lists li {
  height: 50px;
  line-height: 50px;
  /* text-align: center; */
  width: 100%;
  display: flex;
}
.con .lists li:nth-child(odd) {
  background-color: #f7f7f7;
}

/* .con .lists li div {
} */
.con .lists li div.num {
  width: 4%;
}
.con .lists li div.time {
  width: 6%;
}
.con .lists li div.song,
.con .lists li div.artist,
.con .lists li div.album {
  width: 30%;
}
</style>
